*,
* {
  margin: 0;
  padding: 0;
}
body {
  background-image: url("/src/components/img/An-Interstellar-Journey.jpg") ;

}
.header {
  display: flex;
  background-color: rgba(0, 0, 0, 0.32);
  min-height: 40px;
  max-height: 40px;
  overflow: hidden;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}
/* left container header */
.header__left-conteiner {
  display: flex;
  flex-grow: 1;
}
.left-conteiner__main,
.left-conteiner__home,
.left-conteiner__board,
.header__right-conteiner__plus,
.header__right-conteiner__information,
.header__right-conteiner__notification,
.header__right-conteiner__avatar {
  border-radius: 3px;
  border: 0;
  text-decoration: none;
  align-items: center;
  background-color: rgba(255, 255, 255, 0.3);
  box-shadow: none;
  color: #ffffff;
  display: flex;
  font-weight: bold;
  height: 32px;
  line-height: 32px;
  margin: 0 5px 0 0;
  padding: 0;
  white-space: nowrap;
}
.left-conteiner__main {
  margin-left: 5px;
}
.left-conteiner__main:hover,
.left-conteiner__home:hover,
.left-conteiner__board:hover,
.header__right-conteiner__plus:hover,
.header__right-conteiner__information:hover,
.header__right-conteiner__notification:hover {
  background-color: rgba(255, 255, 255, 0.1);
  box-shadow: none;
  color: #ffffff;
  cursor: pointer;
}
.ico {
  display: flex;
  width: 32px;
  justify-content: center;
  align-items: center;
  margin: 0;
}
.left-conteiner__board-text {
  padding: 0 8px 0 2px;
}
.header__left-conteiner__search {
  position: relative;
}
.search {
  background-color: rgba(255, 255, 255, 0.3);
  border-radius: 3px;
  border: none;
  box-shadow: none;
  box-sizing: border-box;
  color: rgba(255, 255, 255, 0.5);
  float: left;
  font-size: 14px;
  height: 32px;
  line-height: 19px;
  margin: 0;
  outline: none;
  padding-left: 8px;
  padding-right: 30px;
  transition: width 150ms;
  width: 184px;
}
.search-ico {
  position: absolute;
  top: 5px;
  right: 0;
}
/* left container header -- end*/

/* center container header */
.logo-trello {
  display: flex;
}
.logo-trello-text {
  text-decoration: none;
  font-size: 20px;
  font-weight: 900;
  color: #fff;
}
/* center container header -- end */

/* right container header */
.header__right-conteiner {
  display: flex;
  justify-content: flex-end;
  flex-grow: 1;
}
.header__right-conteiner__notification {
  background-color: rgb(253, 48, 48);
}
.header__right-conteiner__avatar {
  border-radius: 50%;
}
.right-conteiner__avatar {
  margin: 0 5px;
}
/* right container header -- end*/

/* board */
.board {
  display: flex;
}
.list-wraper:first-child {
  margin-left: 10px;
}
.list-wraper {
  width: 272px;
  margin: 0 4px;
  height: 100%;
  box-sizing: border-box;
  display: inline-block;
  vertical-align: top;
  white-space: nowrap;
}
.list {
  background-color: #ebecf0;
  border-radius: 3px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  max-height: 100%;
  position: relative;
  white-space: normal;
  padding: 4px 5px;
}
.list-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex: 0 0 auto;
  padding: 10px 8px;
  position: relative;
  min-height: 20px;
}
.card-composer-container {
  display: flex;
  align-items: center;
}
.card-composer-container:hover {
  border-radius: 5px;
  background-color: #6b778c;
  cursor: pointer;
}
.icon-add {
  display: flex;
}
.card-composer-container {
  padding: 5px 10px;
  margin-bottom: 10px;
}
.text-add {
  font-size: 20px;
}
.list-header_icon {
  display: flex;
}
.header-ico {
  padding: 4px;
  display: flex;
}
.header-ico:hover,
.delite:hover {
  border-radius: 5px;
  background-color: rgb(145, 143, 138);
  cursor: pointer;
}
.delite {
  font-size: 30px;
  font-weight: 900;
  padding: 0 7px;
}
.open-add {
  margin: 0 4px;
}
.open-list {
  margin-left: 4px;
}
.open-add-list {
  display: flex;
  align-items: end;
  padding: 10px;
  background-color: rgba(0, 0, 0, 0.32);
  border-radius: 5px;
}
.open-add-list:hover {
  background-color: rgb(219, 214, 214);
  cursor: pointer;
}

/*modal styles*/
.modal {
  font-family: "Times New Roman";
  font-size: 20px;
  background-color: #fefefe;
  margin: 0 auto;
  padding-bottom: 20px;
  border: 1px solid #888;
  width: 30%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.modal p {
  margin-top: 20px;
  margin-left: 10px;
  padding-bottom: 10px;
}

.modal__buttons {
  margin-top: 10px;
}

.modal__button {
  padding: 10px;
}
.close-modal {
  background-color: #6b778c;
  box-shadow: none;
  border: none;
  color: #000;
  font-family: "Times New Roman";
  line-height: 20px;
  font-weight: 400;
  box-sizing: border-box;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border-radius: 5px;
  cursor: pointer;
  padding: 6px 12px;
  margin: 0 6px 10px  5px;
  font-size: 20px;
}

.delete  {
  background-color: #884a4a;
  box-shadow: none;
  border: none;
  color: #000;
  line-height: 20px;
  font-weight: 400;
  box-sizing: border-box;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border-radius: 5px;
  cursor: pointer;
  padding: 6px 12px;
  margin: 0 6px 10px 12px;
  font-family: "Times New Roman";
  font-size: 20px;
}
.deleteAll {
  background-color: #884a4a;
  box-shadow: none;
  border: none;
  color: #000;
  line-height: 20px;
  font-weight: 400;
  box-sizing: border-box;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border-radius: 5px;
  cursor: pointer;
  padding: 6px 12px;
  margin: 0 6px 10px 12px;
  font-family: "Times New Roman";
  font-size: 20px;
}
.close {
  margin-right: 7px;
  text-align: right;
  color: #aaa;
  float: right;
  font-size: 30px;
  font-weight: bold;
}
.close:hover,
.close:focus {
  color: black;
  text-decoration: none;
  cursor: pointer;
}

.back-card:hover {
    border-radius: 5px;
    background-color: rgb(145, 143, 138);
    cursor: pointer;
  }
  .back-card {
      margin-right: 6px;
    font-size: 30px;
    font-weight: 1000;
    padding: 0 7px;
    transform: scale(-1, 1)
  }

  .transfer-card:hover {
    border-radius: 5px;
    background-color: rgb(145, 143, 138);
    cursor: pointer;
  }
  .transfer-card {
    
    margin-right: 6px;
    font-size: 30px;
    padding: 0 0 0 7px;
    font-weight: 1000;
   
    
  }
  @media screen and (max-width: 756px) {
    .board{
      flex-wrap: wrap;
    }
    .list-wraper{
      margin-bottom: 10px;
    }
    .list-wraper:first-child {
     margin-left: 4px;
    }
  }
  @media screen and (max-width: 556px){
  .board{
   justify-content: center;
  }
  }
  @media screen and (max-width: 550px){
   .search, .search-ico{
   display: none;
   }
   .modal {
     width: 50%;
   }
   }